import { getRequestHeaders } from '@sillytavern/script';
import { t } from '@sillytavern/scripts/i18n';

import { extensionName } from '@/util/extension_variables';

/**
 * 
 */
export async function updateFrontendVersion() {
  const response = await fetch('/api/extensions/update', {
    method: 'POST',
    headers: getRequestHeaders(),
    body: JSON.stringify({ extensionName: extensionName }),
  });
  if (!response.ok) {
    const text = await response.text();
    // @ts-ignore
    toastr.error(text || response.statusText, t``, { timeOut: 5000 });
    console.error(`: ${text}`);
    return false;
  }

  const data = await response.json();
  if (data.isUpToDate) {
    console.info(`, `);
  } else {
    // @ts-ignore
    toastr.success(t` ${data.shortCommitHash}`, t``);
    console.info(`  ${data.shortCommitHash}, `);
  }
  return true;
}
